import React from 'react';
import { render, screen, act, fireEvent } from '@testing-library/react-native';
import { Text, Button } from 'react-native';
import { ThemeProvider, ThemeContext } from '../../src/contexts/ThemeContext';

// Mock react-native's useColorScheme
jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  return {
    ...RN,
    useColorScheme: jest.fn(() => 'light'), // Default system theme
  };
});

// Mock storageUtils
jest.mock('../../src/utils/storageUtils', () => ({
  storeData: jest.fn(() => Promise.resolve()),
  getData: jest.fn(() => Promise.resolve(null)),
}));

// Mock constants
jest.mock('../../src/utils/constants', () => ({
  COLORS: {
    background: '#FFFFFF',
    text: '#000000',
    // ... other default light colors
  },
}));

// Import mocked functions
import { useColorScheme } from 'react-native';
import { storeData, getData } from '../../../src/utils/storageUtils';

// Helper component to consume the context
const TestComponent = () => {
  const { theme, isDarkTheme, colors, loaded, setAppTheme, toggleTheme, resetToSystemTheme } = React.useContext(ThemeContext);

  if (!loaded) {
    return <Text testID="loading-status">Loading Theme...</Text>;
  }

  return (
    <>
      <Text testID="current-theme">Theme: {theme}</Text>
      <Text testID="is-dark-theme">Is Dark: {isDarkTheme ? 'Yes' : 'No'}</Text>
      <Text testID="background-color">Background: {colors.background}</Text>
      <Text testID="text-color">Text: {colors.text}</Text>

      <Button title="Set Light" onPress={() => setAppTheme('light')} />
      <Button title="Set Dark" onPress={() => setAppTheme('dark')} />
      <Button title="Set System" onPress={() => setAppTheme('system')} />
      <Button title="Toggle Theme" onPress={toggleTheme} />
      <Button title="Reset to System" onPress={resetToSystemTheme} />
    </>
  );
};

describe('ThemeContext', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    getData.mockResolvedValue(null); // Reset mock before each test
    useColorScheme.mockReturnValue('light'); // Default system scheme
    jest.useFakeTimers(); // Enable fake timers for setTimeout
  });

  afterEach(() => {
    jest.runOnlyPendingTimers(); // Run any pending timers
    jest.useRealTimers(); // Restore real timers
  });

  it('loads default theme (system) if no saved theme is found', async () => {
    render(
      <ThemeProvider>
        <TestComponent />
      </ThemeProvider>
    );

    expect(screen.getByTestId('loading-status')).toBeOnTheScreen();

    await act(async () => {
      jest.runAllTimers(); // Allow useEffect to run and finish loading
    });

    expect(screen.queryByTestId('loading-status')).toBeNull();
    expect(screen.getByTestId('current-theme').props.children).toBe('Theme: system');
    expect(screen.getByTestId('is-dark-theme').props.children).toBe('Is Dark: No');
    expect(screen.getByTestId('background-color').props.children).toBe('Background: #FFFFFF');
    expect(getData).toHaveBeenCalledWith('appTheme');
    expect(storeData).not.toHaveBeenCalled(); // Should not save on initial load if no changes
  });

  it('loads saved theme from storage', async () => {
    getData.mockResolvedValueOnce('dark');

    render(
      <ThemeProvider>
        <TestComponent />
      </ThemeProvider>
    );

    await act(async () => {
      jest.runAllTimers();
    });

    expect(screen.getByTestId('current-theme').props.children).toBe('Theme: dark');
    expect(screen.getByTestId('is-dark-theme').props.children).toBe('Is Dark: Yes');
    expect(screen.getByTestId('background-color').props.children).toBe('Background: #121212'); // Dark theme background
  });

  it('sets theme to light and saves it', async () => {
    render(
      <ThemeProvider>
        <TestComponent />
      </ThemeProvider>
    );
    await act(async () => { jest.runAllTimers(); });

    fireEvent.press(screen.getByText('Set Light'));
    expect(screen.getByTestId('current-theme').props.children).toBe('Theme: light');
    expect(screen.getByTestId('is-dark-theme').props.children).toBe('Is Dark: No');
    expect(screen.getByTestId('background-color').props.children).toBe('Background: #FFFFFF');

    await act(async () => { jest.runAllTimers(); }); // Trigger debounce save
    expect(storeData).toHaveBeenCalledWith('appTheme', 'light');
  });

  it('sets theme to dark and saves it', async () => {
    render(
      <ThemeProvider>
        <TestComponent />
      </ThemeProvider>
    );
    await act(async () => { jest.runAllTimers(); });

    fireEvent.press(screen.getByText('Set Dark'));
    expect(screen.getByTestId('current-theme').props.children).toBe('Theme: dark');
    expect(screen.getByTestId('is-dark-theme').props.children).toBe('Is Dark: Yes');
    expect(screen.getByTestId('background-color').props.children).toBe('Background: #121212');

    await act(async () => { jest.runAllTimers(); }); // Trigger debounce save
    expect(storeData).toHaveBeenCalledWith('appTheme', 'dark');
  });

  it('toggles theme correctly from light to dark', async () => {
    render(
      <ThemeProvider>
        <TestComponent />
      </ThemeProvider>
    );
    await act(async () => { jest.runAllTimers(); });

    fireEvent.press(screen.getByText('Set Light')); // Ensure it's light first
    await act(async () => { jest.runAllTimers(); });

    fireEvent.press(screen.getByText('Toggle Theme'));
    expect(screen.getByTestId('current-theme').props.children).toBe('Theme: dark');
    expect(screen.getByTestId('is-dark-theme').props.children).toBe('Is Dark: Yes');
  });

  it('toggles theme correctly from dark to light', async () => {
    render(
      <ThemeProvider>
        <TestComponent />
      </ThemeProvider>
    );
    await act(async () => { jest.runAllTimers(); });

    fireEvent.press(screen.getByText('Set Dark')); // Ensure it's dark first
    await act(async () => { jest.runAllTimers(); });

    fireEvent.press(screen.getByText('Toggle Theme'));
    expect(screen.getByTestId('current-theme').props.children).toBe('Theme: light');
    expect(screen.getByTestId('is-dark-theme').props.children).toBe('Is Dark: No');
  });

  it('toggles theme from system based on systemColorScheme', async () => {
    useColorScheme.mockReturnValue('dark'); // Simulate system dark mode
    render(
      <ThemeProvider>
        <TestComponent />
      </ThemeProvider>
    );
    await act(async () => { jest.runAllTimers(); });

    expect(screen.getByTestId('current-theme').props.children).toBe('Theme: system');
    expect(screen.getByTestId('is-dark-theme').props.children).toBe('Is Dark: Yes');

    fireEvent.press(screen.getByText('Toggle Theme'));
    expect(screen.getByTestId('current-theme').props.children).toBe('Theme: light'); // Toggles to light from system dark
    expect(screen.getByTestId('is-dark-theme').props.children).toBe('Is Dark: No');
  });

  it('resets to system theme and saves it', async () => {
    render(
      <ThemeProvider>
        <TestComponent />
      </ThemeProvider>
    );
    await act(async () => { jest.runAllTimers(); });

    fireEvent.press(screen.getByText('Set Dark')); // Change to dark first
    await act(async () => { jest.runAllTimers(); });

    fireEvent.press(screen.getByText('Reset to System'));
    expect(screen.getByTestId('current-theme').props.children).toBe('Theme: system');
    expect(screen.getByTestId('is-dark-theme').props.children).toBe('Is Dark: No'); // System is light by default mock

    await act(async () => { jest.runAllTimers(); }); // Trigger debounce save
    expect(storeData).toHaveBeenCalledWith('appTheme', 'system');
  });
});
